//
// Namespace root
//

& {
  // Sets a specific default `font-size` for user with `em` type scales.
  font-size: $font-size-root;
  // Changes the default tap highlight to be completely transparent in iOS.
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: $line-height;
  color: $body-color;
  background-color: $body-bg !important;
  display: block;
  overflow:hidden;
  vertical-align: top;
  position: relative;
  padding: 0;
  // font smoothing
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  width: 100%;
  height:0;
  @include vendor-prefixes(transition, padding-top 0.5s ease);
  min-width:$min-player-width;
  // default aspect ratio 16:9
  @include set-aspect-ratio(16, 9);
}

&:-moz-full-screen {
  position: absolute;
  width: 100% !important;
  height: 100% !important;
  padding:0 !important;
}

&:-webkit-full-screen {
  width: 100% !important;
  height: 100% !important;
  padding:0 !important;
}

&:-ms-fullscreen {
  width: 100% !important;
  height: 100% !important;
  padding:0 !important;
}

&.fullscreen {
  position:fixed;
  overflow: hidden;
  top:0;
  left:0;
  width:100%;
  height:100%;
}


//
// Links
//

a {
  color: $link-color;
  text-decoration: $link-decoration;
  cursor: pointer;

  @include hover-focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
  }

  &:focus {
    @include tab-focus();
  }
}

.col-sm-2 {
  width: (100/12)*2%;
}

.col-sm-10 {
  width: (100/12)*10%;
}

.col-sm-offset-2 {
  margin-left: (100/12)*2%;
}